import uniList from "@/components/uni-list/uni-list"
import rfImage from "@/components/rf-image/rf-image"
import uniListItem from "@/components/uni-list-item/uni-list-item"
import {merchantView} from "@/api/merchant";
export default {
	data() {
		return {
			aboutInfo: {},
			userInfo:{},
			navList: [
				{ title: '商城介绍', url: '' },
				// { title: '版权信息', url: 'copyright_companyname,copyright_desc,copyright_url' },
				// { title: '证照信息', url: '' },
				{ title: '注册协议', url: 'protocol_register' },
				{ title: '充值协议', url: 'protocol_recharge' },
				{ title: '隐私协议', url: 'protocol_privacy' }
			]
		}
	},
	components: {
		uniList,
		uniListItem,
		rfImage
	},
	onLoad() {
		this.initData();
	},
	methods: {
		// 初始化数据
		initData () {
			this.userInfo=uni.getStorageSync('userInfo')
			//this.token = uni.getStorageSync('accessToken') || undefined;
			this.token=this.userInfo.id
			if (this.token) {
				this.getConfigList();
			}
		},
		// 获取商城信息
		async getConfigList() {
	    const userInfo = uni.getStorageSync('userInfo');
	    if (!userInfo) return;
			
			await this.$get(`${merchantView}`, {
				id: userInfo.merchant_id,
				field: 'web_qrcode'
			}).then(r => {
				this.aboutInfo = r.data
			}).catch(err => {
				console.log(err)
			});
		},
		// 统一跳转接口
		navTo(url){
			uni.navigateTo({
				url
			})
		},
	}
}
